import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import * as serviceWorker from './serviceWorker';
import { MainStore } from './stores/MainStore';

const mainStore = new MainStore();
mainStore.loadHolidays();
mainStore.clearCompletedOffs();
mainStore.loadFromLocalStorage();

setInterval(() => {
	mainStore.updateCurrentDate();
}, 1000 * 60);

setInterval(() => {
	mainStore.saveToLocalStorage();
}, 1000 * 10);

ReactDOM.render(<App mainStore={mainStore} />, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();