import React, { Component } from 'react';
import moment from 'moment';
import styled from 'styled-components';
import { inject, observer } from 'mobx-react';
import DaysPanel from './DaysPanel';

const StyledContainer = styled.div`
	padding: 1rem 2rem 2rem 2rem;
	display: flex;
	overflow-x: scroll;

	& > * {
		flex-grow: 0;
	}
`;

@inject('mainStore')
@observer
class DaysPanelContainer extends Component {
	deleteOff = (day) => {
		this.props.mainStore.deleteOff(day);
	}

	render() {
		return (
			<StyledContainer>
				{this.props.days.map(function(day) {
					return (<DaysPanel
						key={day.date}
						date={day.date}
						name={day.name}
						daysLeft={-moment().diff(day.date, 'days')}
						type={day.type}
						deletable={this.props.deletable}
						onDelete={this.deleteOff.bind(this, day)} />);
				}, this) }
			</StyledContainer>
		);
	}
}
export default DaysPanelContainer;