import React, { Component } from 'react';
import styled from 'styled-components';
import DayPickerInput from 'react-day-picker/DayPickerInput';
import 'react-day-picker/lib/style.css';
import { formatDate, parseDate } from 'react-day-picker/moment'
import { DateUtils } from 'react-day-picker';
import { inject } from 'mobx-react';

const StyledDayPickerInput = styled(DayPickerInput)``;

const DayPickerDiv = styled.div`
	label {
		display: block;
		color: white;
		font-size: 1.5rem;
		margin-bottom: 0.5rem;
	}

	input {
		background-color: rgba(0, 0, 0, 0);
		outline: none;
		padding: 0.5rem;
		border: 1px solid white;
		border-radius: 2px;
		font-size: 2rem;
		color: white;
		width: 15rem;
		text-align: center;
		font-weight: 300;
	}
`;

const FORMAT = "DD/MM/YY";

@inject("mainStore")
class LabelledDayPicker extends Component {
	render() {
		return (
			<DayPickerDiv>
				<label>{this.props.label}</label>
				<StyledDayPickerInput
					placeholder=""
					format={FORMAT}
					formatDate={formatDate}
					parseDate={parseDate}
					onDayChange={this.props.onDayChange} />
			</DayPickerDiv>
		);
	}
}

export default LabelledDayPicker;