import React, { Component } from 'react';
import styled from 'styled-components';
import { inject, observer } from 'mobx-react';
import DaysPanelContainer from './DaysPanelContainer';
import moment from 'moment';
import AddDateButton from './AddDateButton';
import AddOffModal from './AddOffModal';

const StyledButton = styled(AddDateButton)`
	position: absolute;
	top: 2rem;
	right: 2rem;
`;

const Container = styled.div`
	padding-top: 2rem;

	h1 {
		margin-left: 2rem;
		margin-top: 0;
		margin-bottom: 0;
		font-size: 1.5rem;

		span:before {
			content: '-';
			color: lightgrey;
			margin-left: 0.5rem;
			margin-right: 0.7rem;
		}

		span {
			font-weight: 400;
		}
	}

	position: relative;
	box-shadow: 0 5px 5px rgb(0, 0, 0, 0.1);
`;

@inject('mainStore')
@observer
class Offs extends Component {
	state = {
		modalOpen: false
	}

	handleClick = () => {
		if (!this.state.modalOpen) {
			this.setState({modalOpen: true});
		}
	}

	closeModal = () => {
		if (this.state.modalOpen) {
			this.setState({modalOpen: false});
		}
	}

	addOff = (name, type, day) => {
		this.props.mainStore.addOff(name, type, day);
	}

	render() {
		return (<Container>
			<StyledButton onClick={this.handleClick}/>
			<AddOffModal addOff={this.addOff} isOpen={this.state.modalOpen} closeModal={this.closeModal}/>
			<h1>OFFS <span>{this.props.mainStore.numberOfOffs} OFF{this.props.mainStore.numberOfOffs == 1 || "S"} &nbsp;&amp;&nbsp; {this.props.mainStore.numberOfLeaves} LEAVE{this.props.mainStore.numberOfLeaves == 1 || "S"}</span></h1>
			<DaysPanelContainer deletable days={this.props.mainStore.filteredOffs} />
		</Container>);
	}
}

export default Offs;