import React, { Component } from 'react';
import Modal from 'react-modal';
import styled from 'styled-components';
import DayPicker from 'react-day-picker';
import Select from 'react-select';

const customStyles = {
	content: {
		padding: 0,
		display: "flex",
		flexDirection: "column",
		height: "55rem"
	}
}

const Header = styled.div`
	padding: 2rem;

	h1 {
		margin: 0;
		font-weight: bold;
	}
`;

const Button = styled.button`
	position: absolute;
	bottom: 2rem;
	border: none;
	font-size: 2rem;
	padding: 1rem;
	width: 10rem;
	border-radius: 2px;
`;

const AddButton = styled(Button)`
	right: 2rem;
	background-color: #218c74;
	color: white;
	font-weight: 300;
`;

const CancelButton = styled(Button)`
	left: 2rem;
`;

const Body = styled.div`
	padding: 2rem;
	padding-top: 0rem;
	text-align: center;
	position: relative;

	flex: 1 0 auto;

	label {
		font-size: 1.5rem;
		margin-bottom: 0.5rem;
		display: block;
		text-align: left;
	}

	#event-name {
		width: 100%;
		padding: 0.5rem 1rem;
		font-size: 2rem;
		border-radius: 2px;
		border: 1px solid #eee;
	}

	#event-type {
		margin-top: 1rem;
		font-size: 2rem;
		border-radius: 2px;
	}

	.day-picker {
		font-size: 1.5rem;
		margin-top: 2rem;
	}
`;

class AddOffModal extends Component {

	state = {
		selectedDay: undefined,
		eventName: "",
		eventType: ""
	}

	handleNameChange = (e) => {
		this.setState({eventName: e.target.value});
	}

	handleTypeChange = (option) => {
		this.setState({eventType: option.value});
	}

	handleDayClick = (day) => {
		this.setState({selectedDay: day});
	}

	handleAddOff = () => {
		this.props.addOff(this.state.eventName, this.state.eventType, this.state.selectedDay);
		this.props.closeModal();
	}

	render() {
		return (
			<Modal
				isOpen={this.props.isOpen}
				onRequestClose={this.props.closeModal}
				style={customStyles}
				ariaHideApp={false}
			>
				<Header>
					<h1>ADD OFF / LEAVE</h1>
				</Header>
				<Body>
					<label htmlFor="event-name">Event Name</label>
					<input id="event-name" onChange={this.handleNameChange}></input>
					<Select id="event-type" onChange={this.handleTypeChange} options={[{value: "off", label: "Off"}, {value: "leave", label: "Leave"}]}/>
					<DayPicker className="day-picker" disabledDays={{daysOfWeek: [0, 6]}} selectedDays={this.state.selectedDay} onDayClick={this.handleDayClick}/>
					<AddButton onClick={this.handleAddOff}>Add</AddButton>
					<CancelButton onClick={this.props.closeModal}>Cancel</CancelButton>
				</Body>
			</Modal>
		);
	}
}

export default AddOffModal;