import React, { Component } from 'react';
import styled from 'styled-components';
import DaysPanelContainer from './DaysPanelContainer';
import { inject, observer } from 'mobx-react';
import moment from 'moment';

const Container = styled.div`
	h1 {
		margin-left: 2rem;
		margin-top: 0;
		padding-top: 2rem;
		margin-bottom: 0;
		font-size: 1.5rem;
	}

	background-color: #fff;

	box-shadow: 0 5px 5px rgb(0, 0, 0, 0.1);
`;

const Holidays = inject('mainStore')(observer((props) => (
	<Container>
		<h1>HOLIDAYS</h1>
		<DaysPanelContainer days={props.mainStore.holidays.filter(day => moment().isBefore(day.date))} />
	</Container>
)));

export default Holidays;