import React, { Component } from 'react';
import styled from 'styled-components';

const CountdownDiv = styled.div`
	margin: 0 auto;
	text-align: center;

	.days {
		font-size: 5rem;
	}
	
	.label {
		font-size: 1.5rem;
	}
`;

const MainCountdown = (props) => (
	<CountdownDiv>
		<div className="days">{props.days}</div>
		<div className="label">{props.days === 0 ? "ORD LO!" : "DAYS TO ORD"}</div>
	</CountdownDiv>
);

export default MainCountdown;