import React, { Component } from 'react';
import styled, { css } from 'styled-components';
import moment from 'moment';
import { MdClose } from 'react-icons/md'

const Number = styled.div`
	font-size: 4rem;
	flex: 0 0 auto;
	margin-right: 0.8rem;
`;

const Right = styled.div`
	flex: 1 0 auto;
	align-content: flex-start;
	margin-top: 0.7rem;
`;

const DaysToLabel = styled.div`
	font-size: 1rem;
`;

const DayName = styled.div`
	font-size: 2rem;
	float: left;

	&::after {
		content: 'Â·';
		color: lightgrey;
		font-weight: bold;
		margin-left: 0.5rem;
		margin-right: 0.5rem;
	}
`;

const DayDate = styled.div`
	float: left;
	font-size: 1.5rem;
	line-height: 2.5rem;
	font-weight: 300;
	color: lightgrey;
`;

const StyledPanel = styled.div`
	display: flex;
	
	flex: 1 0 auto;
	&:not(:last-child) {
		margin-right: 1rem;
	}

	background-color: #fafafa;
	border-radius: 0.5rem;
	padding: 0.5rem 2rem;

	button {
		flex: 0 0 auto;
		background-color: rgba(0, 0, 0, 0);
		border: none;
		font-size: 2rem;
		margin-left: 1rem;
		color: grey;
		margin-top: 0.5rem;
	}

	${props => props.type === "leave" && css`
		background-color: #84817a;
		color: white;

		${DayDate} {
			color: white;
		}

		button {
			color: white;
		}
	`}
`;

const DaysPanel = (props) => (
	<StyledPanel type={props.type || ""}>
		<Number>{props.daysLeft}</Number>
		<Right>
			<DaysToLabel>DAYS TO</DaysToLabel>
			<DayName>{props.name}</DayName>
			<DayDate>{moment(props.date).format('DD/MM/YY')}</DayDate>
		</Right>
		{ props.deletable && <button onClick={props.onDelete}><MdClose /></button> }
	</StyledPanel>
);

export default DaysPanel;