import { observable, computed, action, runInAction } from 'mobx';
import moment from 'moment';

export class MainStore {
	constructor() {

	}

	@observable enlistmentDate = new Date(2018, 3, 10, 0, 0);
	@observable currentDate = new Date();
	@observable ordDate = new Date(2020, 1, 9, 0, 0);
	@observable holidays = [];
	@observable offs = [];
	@observable milestones = [];

	@computed get daysToOrd() {
		return moment(this.ordDate).diff(moment(this.currentDate), 'days');
	}

	@computed get percentageToOrd() {
		return ((this.currentDate - this.enlistmentDate) / (this.ordDate - this.enlistmentDate) * 100).toFixed(1);
	}

	@computed get numberOfOffs() {
		return this.offs.filter(off => off.type === "off").length;
	}

	@computed get numberOfLeaves() {
		return this.offs.filter(off => off.type === "leave").length;
	}

	@computed get filteredOffs() {
		console.log(this.offs.sort().filter(off => moment().isBefore(off.date)));
		return this.offs.sort((a, b) => a.date.localeCompare(b.date)).filter(off => moment().isBefore(off.date));
	}

	@action updateEnlistmentDate(day) {
		this.enlistmentDate = day;
		this.saveToLocalStorage();
	}

	@action updateOrdDate(day) {
		this.ordDate = day;
		this.saveToLocalStorage();
	}

	@action clearCompletedOffs() {
		this.offs = this.filteredOffs;
	}

	@action addOff(name, type, day) {
		this.offs.push({date: moment(day).format("YYYY-MM-DD"), name, type});
		this.saveToLocalStorage();
	}

	@action deleteOff(obj) {
		this.offs = this.offs.filter(off => {
			return !(off.date == obj.date && off.name == obj.name && off.type == obj.type);
		});
		this.saveToLocalStorage();
	}

	@action updateCurrentDate() {
		this.currentDate = new Date();
	}
	
	@action loadFromLocalStorage() {
		const offs = window.localStorage.getItem('offs');
		if (offs === null) {
			this.offs = [];
		} else {
			this.offs = JSON.parse(offs);
		}

		const dates = window.localStorage.getItem('dates');
		if (dates === null) {
			this.enlistmentDate = new Date(2018, 3, 10);
			this.ordDate = new Date(2020, 1, 9);
		} else {
			const datesObj = JSON.parse(dates);
			this.enlistmentDate = moment(datesObj.enlistmentDate).toDate();
			this.ordDate = moment(datesObj.ordDate).toDate();
		}
	}

	@action saveToLocalStorage() {
		window.localStorage.setItem('offs', JSON.stringify(this.offs));
		window.localStorage.setItem('dates', JSON.stringify({
			enlistmentDate: moment(this.enlistmentDate).format(),
			ordDate: moment(this.ordDate).format()
		}));
	}
	
	@action async loadHolidays() {
		this.holidays = [
			{name: "Deepavali", date: "2018-11-06"},
			{name: "Christmas", date: "2018-12-25"},
			{name: "New Year's Day", date: "2019-01-01"}
		]
	}
}