import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import styled from 'styled-components';
import MainCountdown from './MainCountdown';
import CircleProgressBar from './CircleProgressBar';
import LabelledDayPicker from './LabelledDayPicker';

const StyledHeader = styled.div`
	padding: 2.5rem 2.5rem;
	background-color: #218c74;
	box-shadow: 0px 5px 5px rgb(0, 0, 0, 0.1);
`;

const DatePickers = styled.div`
	display: flex;
	justify-content: center;
	padding-top: 2rem;
	
	& > * {
		flex: 0 0 auto;
	}

	& > *:not(:last-child) {
		padding: 0 2rem 0 0;
	}
`;

@inject("mainStore")
@observer
class Header extends Component {
	render() {
		return (
			<StyledHeader>
				<CircleProgressBar size="20rem" percent={this.props.mainStore.percentageToOrd} strokeColor="#fff" trailColor="#fff" strokeWidth="3" strokeLinecap="square" gapDegree="70" gapPosition="bottom">
					<MainCountdown days={this.props.mainStore.daysToOrd} />
				</CircleProgressBar>
				<DatePickers>
					<LabelledDayPicker label="Enlistment Date" onDayChange={day => this.props.mainStore.updateEnlistmentDate(day)} />
					<LabelledDayPicker label="ORD Date" onDayChange={day => this.props.mainStore.updateOrdDate(day)}/>
				</DatePickers>
			</StyledHeader>
		);
	}
}

export default Header;